@import url(https://fonts.googleapis.com/css2?family=Iceland&family=Rubik&display=swap);
body {
  margin: 0;
}

.App {
  font-family: 'Rubik', sans-serif;
  font-size: 14px;
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  background-color: #eef;
}

.App-header {
  width: 100%;
  height: 36px;
  box-sizing: border-box;
  display: flex;
  background-color: white;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 0px 1px 20px #777;
}

.App-title {
  margin-left: 24px;
  font-family: 'Iceland', sans-serif;
  font-size: 34px;
  font-weight: bold;
  padding-bottom: 0;
  text-shadow: 2px 2px #5e5;
  color: #93b;
}

.App-top {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.App-canvas {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  padding: 20px 0px 0px 20px;
  margin: 0;
  box-sizing: border-box;
  width: 40%;
}

.App-editor {
  flex: 2 2;
  text-align: left;
  padding: 20px;
  margin: 0;
  box-sizing: border-box;
  width: 60%;
}

.App-bottom {
  background-color: none;
  height: 48px;
}

.ControlPanel {
  margin-left: 20px;
  display: flex;
  flex-direction: row;
  justify-content: left;
}

.ControlPanel .Toggle {
  width: 32px;
  height: 32px;
}

.ControlPanel .OptionsPanel {
  position: absolute;
  bottom: 60px;
  z-index: 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: solid gray 1px;
  background: #fff;
  padding: 1em;
  border-radius: 4px;
}

.ControlPanel .LoadBlueprintPanel {
  position: absolute;
  left: 120px;
  bottom: 60px;
  z-index: 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  border: solid gray 1px;
  background: #fff;
  padding: 1em;
  border-radius: 4px;
}

.ControlPanel .LoadBlueprintPanel h1 {
  margin: 0;
  font-weight: bold;
  font-size: 1.5em;
}

.ControlPanel .LoadBlueprintPanel .LoadMenuItem {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 4px;
  box-sizing: border-box;
  border: solid #999 2px;
  padding: 3px;
  background-color: #eee;
}

.ControlPanel .LoadBlueprintPanel .LoadMenuItem:hover {
  background-color: #efe;
}

.ControlPanel .LoadBlueprintPanel .LoadMenuItem .LoadMenuName {
  flex: 1 1;
  margin: 0;
  padding: 0;
  text-align: left;
  font-size: 1.4em;
}

.ControlPanel .LoadBlueprintPanel .LoadMenuItem .LoadMenuRemove {
  width: 12px;
  height: 12px;
  padding: 0;
  line-height: 0;
}

.ControlPanel .OptionsPanel h1 {
  margin: 0;
  font-weight: bold;
  font-size: 1.2em;
}

.ControlPanel .OptionsPanel h2 {
  font-weight: bold;
  font-size: 1em;
}

.ControlPanel .PasteBlueprintPanel {
  position: absolute;
  left: 120px;
  bottom: 60px;
  z-index: 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  border: solid gray 1px;
  background: #fff;
  padding: 1em;
  border-radius: 4px;
}

.ControlPanel .PasteBlueprintPanel textarea {
  height: 3em;
}

.LabeledField {
  margin: 4px 4px 0 4px;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: flex-end;
}

.LabeledField > .Label {
  font-weight: bold;
  margin-right: 1em;
}

.LabeledField > .Field {
  flex: 1 1;
}

.Display {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  width: 100%;
  box-sizing: border-box;
}

.Display .CanvasWrapper {
  display: flex;
  box-sizing: border-box;
  border-radius: 8px;
}

.Display .CanvasWrapper canvas {
  position: absolute;
  border-radius: 8px;
  background-color: black;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}

.Editor {
  box-sizing: border-box;
  height: 100%;
}

.BlueprintEditor {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: #2f1e2e;
}

.BlueprintEditor .Toolbar {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 10;
}

.BlueprintEditor .Toolbar button {
  min-width: 3em;
  height: 3em;
}

.Node {
  padding: 4px 0px 2px 0px;
  background-image: linear-gradient(20deg, #888, #ddd);
  border: outset #aaa 2px;
  border-radius: 8px 12px 12px 1px;
  display: flex;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  flex-direction: column;
  align-items: stretch;
  box-shadow: 8px 8px 15px 2px #00000040;
}

.Node select {
  text-align: right;
}

.Node-buffer {
  background-image: linear-gradient(20deg, #9c9, #dfd);
  border-color: #beb;
}

.Node-render {
  background-image: linear-gradient(20deg, #955, #fdd);
  border-color: #ebb;
}

.Node-render input[type='number'] {
  width: 5em;
  text-align: right;
}

.Node-compute {
  background-image: linear-gradient(20deg, #99b, #dff);
  border-color: #eef;
}

.Node-compute .DispatchSize {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.Node-compute .DispatchSize input[type='number'] {
  width: 5em;
  text-align: right;
}

.Node-texture {
  background-image: linear-gradient(20deg, #d83, #feb);
  border-color: #ec9;
}

.Node-sampler {
  background-image: linear-gradient(20deg, #ee8, #fec);
  border-color: #ffb;
}

.Node > .Title {
  width: 100%;
  height: 9px;
  text-align: left;
  font-weight: bold;
  font-size: 12px;
  line-height: 12px;
  margin: -2px 0 0 6px;
  padding: 0;
}

.Node .RemoveButton,
.Edge .RemoveButton {
  position: absolute;
  top: 4px;
  right: 5px;
  width: 9px;
  height: 9px;
  border: none;
  background: none;
  font-size: 14px;
  font-weight: bold;
  text-shadow: #444 1px 1px;
  color: #600;
  padding: 0;
  line-height: 6px;
}

.RemoveButton::after {
  content: '×';
  margin: -5px 0 0 -1px;
}

.Node > .Content {
  border-radius: 3px;
  background-color: #ffffff80;
  text-align: right;
  margin: 4px;
  padding: 2px 2px 4px 2px;
  font-size: 12px;
}

.Node .EditableLabel {
  max-width: 8em;
  text-align: right;
}

.Edge-queue {
  background: none;
  border: none;
}

.Edge div {
  font-size: 12px;
}

.Edge .Row {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-right: 30px;
}

.Edge input[type='number'] {
  font-size: 12px;
  width: 3em;
  box-sizing: border-box;
}

.Handle {
  width: 8px;
  height: 8px;
}

.Handle.react-flow__handle {
  width: 10px;
  height: 10px;
}

.Handle.Binding.react-flow__handle {
  background: yellow;
}

.Handle.Queue.react-flow__handle {
  background: magenta;
}

.Handle.react-flow__handle-connecting {
  background: #f66 !important;
}

.Handle.react-flow__handle-valid {
  background: #5d9 !important;
}

.Handle.BindingStub.react-flow__handle {
  width: 4px;
  height: 4px;
  background: #444;
  border: solid 1px #888;
}

.Node-texture input[type='file'] {
  margin-top: 8px;
  width: 256px;
}

.Node-texture .TextureDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Node-texture .Preview {
  width: 256px;
  height: 256px;
  border: solid 2px black;
  margin: 8px;
}

.Node-connection {
  width: 120px;
  height: 64px;
}

.Node-connection input[type='number'] {
  width: 3em;
  margin-left: 1em;
  font-weight: bold;
  text-align: center;
  box-sizing: border-box;
}

.Node-connection select {
  margin: 0 0 0 0;
  max-width: 114px;
}

.Node-connection.BufferBinding {
  width: 120px;
  height: 87px;
}

.Edge {
  width: 15px;
  height: 15px;
  background-color: #aaa;
  border-radius: 15px;
}

.Edge .RemoveButton {
  padding: 0 0 1px 1px;
  font-weight: normal;
  font-size: 20px;
  text-shadow: none;
  color: #444;
}

.EditableLabel {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  display: inline;
  background: none;
}

.EditableLabel.NotEditing {
  border: solid transparent 1px;
  outline: none;
  cursor: default;
}

.EditableLabel.Empty {
  color: #999;
}

.EditableLabel.Editing {
  border: solid black 1px;
  background: white;
  outline: none;
}

.CodeEditor {
  position: relative;
  width: 100%;
  height: 100%;
}

.CodeEditor .CodeMirror {
  position: absolute;
  height: 100%;
}

.CodeEditor .MarkedText-info {
  -webkit-text-decoration: #22e double underline;
          text-decoration: #22e double underline;
}

.CodeEditor .MarkedText-warning {
  -webkit-text-decoration: #aa2 double underline;
          text-decoration: #aa2 double underline;
}

.CodeEditor .MarkedText-error {
  -webkit-text-decoration: #d22 double underline;
          text-decoration: #d22 double underline;
}

.CodeEditor .GutterMessages {
  width: 16px;
}

.CodeEditor .Marker {
  margin: 6px;
  width: 8px;
  height: 8px;
}

.CodeEditor .Marker-info {
  background-color: #22e;
}

.CodeEditor .Marker-warning {
  background-color: #aa2;
}

.CodeEditor .Marker-error {
  background-color: #d22;
}

.TabContainer {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  width: 100%;
  height: 100%;
}

.TabContainer .TabStrip {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-start;
  height: 2em;
}

.TabContainer .Tab {
  font-family: 'Rubik', sans-serif;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 6px 6px 0 0;
  padding: 2px 2px 2px 1em;
  height: 2em;
  line-height: 1.8em;
  font-size: 14px;
  box-sizing: border-box;
  max-width: 10em;
}

.TabContainer .Tab .EditableLabel {
  text-align: left;
  display: inline;
  line-height: 1em;
  font-size: 14px;
  font-family: 'Rubik', sans-serif;
  -webkit-appearance: textfield;
          appearance: textfield;
  width: calc(100% - 24px);
}

.TabContainer .Tab .RemoveButton {
  width: 12px;
  height: 12px;
  margin: 3px 0 0 6px;
  line-height: 1.4em;
  padding: 0 0 2px 0;
  background: transparent;
  font-weight: bold;
  border: none;
}

.TabContainer .Tab.Permanent {
  padding-right: 1em;
}

.TabContainer .Tab.Inactive {
  border-right: solid 1px #bbb;
}

.TabContainer .Tab.Inactive.Permanent {
  background: #ccfa;
  border-right: solid 1px #77a;
}

.TabContainer .Tab.Inactive:hover {
  background: #fff7;
}

.TabContainer .Tab.Inactive.Permanent:hover {
  background: #77c8;
}

.TabContainer .Tab.Active {
  background: #fffc;
  border-right: solid 1px #ccc;
}

.TabContainer .Tab.Active.Permanent {
  background: #45a8;
}

.TabContainer .ContentArea {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  flex: 1 1 auto;
  background-color: white;
  width: 100%;
  overflow: hidden;
}

.TabContainer .ContentArea .Content {
  flex: 1 1 auto;
}

.TabContainer .ContentArea .Content.Active {
}

.TabContainer .ContentArea .Content.Inactive {
  position: absolute;
  visibility: hidden;
  width: 10px;
  height: 10px;
}

