.Node {
  padding: 4px 0px 2px 0px;
  background-image: linear-gradient(20deg, #888, #ddd);
  border: outset #aaa 2px;
  border-radius: 8px 12px 12px 1px;
  display: flex;
  user-select: none;
  flex-direction: column;
  align-items: stretch;
  box-shadow: 8px 8px 15px 2px #00000040;
}

.Node select {
  text-align: right;
}

.Node-buffer {
  background-image: linear-gradient(20deg, #9c9, #dfd);
  border-color: #beb;
}

.Node-render {
  background-image: linear-gradient(20deg, #955, #fdd);
  border-color: #ebb;
}

.Node-render input[type='number'] {
  width: 5em;
  text-align: right;
}

.Node-compute {
  background-image: linear-gradient(20deg, #99b, #dff);
  border-color: #eef;
}

.Node-compute .DispatchSize {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.Node-compute .DispatchSize input[type='number'] {
  width: 5em;
  text-align: right;
}

.Node-texture {
  background-image: linear-gradient(20deg, #d83, #feb);
  border-color: #ec9;
}

.Node-sampler {
  background-image: linear-gradient(20deg, #ee8, #fec);
  border-color: #ffb;
}

.Node > .Title {
  width: 100%;
  height: 9px;
  text-align: left;
  font-weight: bold;
  font-size: 12px;
  line-height: 12px;
  margin: -2px 0 0 6px;
  padding: 0;
}

.Node .RemoveButton,
.Edge .RemoveButton {
  position: absolute;
  top: 4px;
  right: 5px;
  width: 9px;
  height: 9px;
  border: none;
  background: none;
  font-size: 14px;
  font-weight: bold;
  text-shadow: #444 1px 1px;
  color: #600;
  padding: 0;
  line-height: 6px;
}

.RemoveButton::after {
  content: '×';
  margin: -5px 0 0 -1px;
}

.Node > .Content {
  border-radius: 3px;
  background-color: #ffffff80;
  text-align: right;
  margin: 4px;
  padding: 2px 2px 4px 2px;
  font-size: 12px;
}

.Node .EditableLabel {
  max-width: 8em;
  text-align: right;
}

.Edge-queue {
  background: none;
  border: none;
}

.Edge div {
  font-size: 12px;
}

.Edge .Row {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-right: 30px;
}

.Edge input[type='number'] {
  font-size: 12px;
  width: 3em;
  box-sizing: border-box;
}

.Handle {
  width: 8px;
  height: 8px;
}

.Handle.react-flow__handle {
  width: 10px;
  height: 10px;
}

.Handle.Binding.react-flow__handle {
  background: yellow;
}

.Handle.Queue.react-flow__handle {
  background: magenta;
}

.Handle.react-flow__handle-connecting {
  background: #f66 !important;
}

.Handle.react-flow__handle-valid {
  background: #5d9 !important;
}

.Handle.BindingStub.react-flow__handle {
  width: 4px;
  height: 4px;
  background: #444;
  border: solid 1px #888;
}

.Node-texture input[type='file'] {
  margin-top: 8px;
  width: 256px;
}

.Node-texture .TextureDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Node-texture .Preview {
  width: 256px;
  height: 256px;
  border: solid 2px black;
  margin: 8px;
}

.Node-connection {
  width: 120px;
  height: 64px;
}

.Node-connection input[type='number'] {
  width: 3em;
  margin-left: 1em;
  font-weight: bold;
  text-align: center;
  box-sizing: border-box;
}

.Node-connection select {
  margin: 0 0 0 0;
  max-width: 114px;
}

.Node-connection.BufferBinding {
  width: 120px;
  height: 87px;
}

.Edge {
  width: 15px;
  height: 15px;
  background-color: #aaa;
  border-radius: 15px;
}

.Edge .RemoveButton {
  padding: 0 0 1px 1px;
  font-weight: normal;
  font-size: 20px;
  text-shadow: none;
  color: #444;
}
